<template>
  <v-app>
    <v-navigation-drawer app v-model="drawer" v-if="isAuthorized">
      <v-list-item>
        <v-list-item-title class="title">
          GAS BANLY
        </v-list-item-title>
        <v-btn icon @click="drawer = false">
          <v-icon>mdi-chevron-left</v-icon>
        </v-btn>
      </v-list-item>
      <v-divider />
      <v-list nav>
        <v-list-item v-for="menu in menus" :key="menu.title" :to="menu.url">
          <v-list-item-icon>
            <v-icon>{{ menu.icon }}</v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title>
              {{ menu.title }}
            </v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>

    </v-navigation-drawer>
    <v-app-bar app color="primary" dark>
      <!-- アプリケーションバー -->
      <v-app-bar-nav-icon @click.stop="drawer = !drawer" v-if="isAuthorized"/>

      <!-- タイトル -->
      <v-toolbar-title>{{ pageTitle }} </v-toolbar-title>

      <v-spacer></v-spacer>
      <!-- テーブルアイコンのボタン -->
      <v-btn icon to="/"> <!-- クリックで "/" へ移動する -->
        <v-icon>mdi-home-outline</v-icon>
      </v-btn>
      <!-- 歯車アイコンのボタン -->
      <v-btn text to="/settings">
        <span class="mr-2">Latest Release</span>
        <v-icon>mdi-cog</v-icon>
      </v-btn>
    </v-app-bar>
    <v-main>
      <v-container fluid>
        <router-view/>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>

export default {
  name: 'App',
  beforeCreate() {
      console.log('beforeCreate');
      this.$store.dispatch('loadSettings');
  },
  mounted() {
    this.$nextTick(() => {
      console.log('mounted App.vue start');
      console.log(this.$route.path);
      console.log(this.menus);
      this.changePageTitle(this.$route.path);
      console.log('mounted App.vue end');
    });
  },
  computed: {
    isAuthorized: function () {
      return this.$store.getters['isAuthorized'];
    },
  },
  watch: {
    '$route.path': function(newVal, oldVal){
      console.log({
        newVal,
        oldVal,
      });
      this.changePageTitle(newVal);
    },
  },
  data() {
      return {
        drawer: false,
        menus: [ ...this.$store.state.menus ],      
        pageTitle: '',
      };
  },
  methods: {
    changePageTitle(url) {
      for (const menu of this.menus) {
        console.log(menu);
        if(menu.url === url){
          this.pageTitle = menu.title;
          break;
        }
      }
    },
  },
};
</script>
