<script>
    import axios from 'axios'
    export default {
        methods: {
            createGasAxios() {
                // 共通のヘッダーを設定したaxiosのインスタンス作成
                const gasApi = axios.create({
                    headers: { 
                    'Accept': 'application/json',
                    'content-type': 'application/x-www-form-urlencoded'
                    },
                })
                gasApi.defaults.baseURL = this.$store.state.settings.apiUrl;
                return gasApi;
            },
            gasPost(action, data) {

                const requestData = {
                    ...data,
                    action,
                    api_key: this.$store.state.settings.authToken,
                };
                console.log({ requestData });
                var params = new URLSearchParams();
                params.append('data', JSON.stringify(requestData));

                const myAxios = this.createGasAxios().post('', params);
                return new Promise((resolve, reject) => {
                    myAxios.then((res) => {
                        console.log(res);
                        resolve(res);
                    });
                    myAxios.catch((err) => {
                        console.log(err);
                        reject(err);
                    });
                });
            },
        },
    };
</script>