<template>
    <div>
        <v-row>
            <v-btn color="primary" @click="onFL003"
                :loading="is_loading">
            FL003
            </v-btn>
        </v-row>
        <v-row>
            <v-data-table :headers="headers"
                          :items="items"
                          :loading="is_loading"
                          dense
                          >
            </v-data-table>
        </v-row>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                isAuthorized: this.$store.state.isAuthorized,
                is_loading: false,
                items: [],
            };
        },
        computed: {
            headers(){
                return [
                { text: '時間', value: 'log_dttm' },
                { text: 'メッセージ', value: 'log_message' },
                ];
            },
        },
        methods: {
            async onFL003(){
                try {
                    this.is_loading = true;
                    this.items = [];
                    const data = {
                        params: {
                            a: 'bbcddd'
                        }
                    };

                    const result = await this.gasPost('FL003', data);
                    if(result.data.status == 'OK'){
                        this.items = result.data.log;
                    }
                } finally {
                    this.is_loading = false;
                }
            },
        },

    };
</script>
