<template>
    <div>
        <h3>アプリ設定</h3>
        <v-row class="mx-4">
            <v-text-field label="API URL" v-model="settings.apiUrl">
            </v-text-field>
        </v-row>
        <v-row class="mx-4">
            <v-text-field label="AUTH TOKEN" v-model="settings.authToken">
            </v-text-field>
        </v-row>
        <v-row class="ma-4">
            <p>※設定はこのデバイスのみに保存されます。</p>
            <v-spacer />
            <v-btn color="primary" @click="onSave" :loading="is_loading">
                保存
            </v-btn>
        </v-row>
    </div>
</template>

<script>
    export default {
        name: 'Settings',
        data() {
            return {
                settings: { ...this.$store.state.settings },
                is_loading: false,
            }
        },
        methods: {
            async onSave(){
                this.$store.dispatch('saveSettings', {
                    settings: this.settings,
                });

                this.notify_info('設定を保存しました');
                await this.onAuthCheck();
            },
            async onAuthCheck() {
                try {
                    this.is_loading = true;
                    
                    const data = {
                        params: {
                        }
                    };
                    const result = await this.gasPost('AUTH_CHECK', data);
                    if(result.data.status == 'OK'){
                        this.$store.dispatch('saveAuthorized', true);
                        this.notify_info('認証に成功しました');
                    }
                } catch (e) {
                    console.log(e);
                } finally {
                    this.is_loading = false;
                }
            },
        },
    }
</script>