import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import vuetify from './plugins/vuetify'

import MyGasApi from './components/MyGasApi.vue'
import MyMessageDialog from './components/MyMessageDialog.vue'
import MyFL003 from './components/MyFL003.vue'
import MyFL014 from './components/MyFL014.vue'

Vue.config.productionTip = false

Vue.mixin(MyGasApi);
Vue.mixin(MyMessageDialog);

Vue.component('MyFL003', MyFL003);
Vue.component('MyFL014', MyFL014);

new Vue({
  router,
  store,
  vuetify,
  render: h => h(App)
}).$mount('#app')
