<script>
    export default {
        methods: {
            async msg_info(message) {
                const res = await this.$dialog.confirm({
                    text: message,
                    title: '情報',
                    actions: { false: '閉じる' },
                    color: 'primary',
                });

                return res;
            },
            notify_info(message) {
                this.$dialog.notify.info(message, {
                    position: 'bottom-right',
                    timeout: 3000,
                });
            },
        },
    };
</script>