import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    isAuthorized: false,
    settings: {
      apiUrl: '',
      authToken: ''
    },
    menus: [
      { title: 'Top', icon: 'mdi-home-outline', url: '/' },
      { title: '設定', icon: 'mdi-cog-outline', url: '/settings' },
    ],
  },
  getters: {
    isAuthorized: state => {
      return state.isAuthorized;
    },
  },
  mutations: {
    /** 設定を保存します */
    saveSettings(state, { settings }){
      state.settings = { ...settings }
      localStorage.setItem('settings', JSON.stringify(settings))
    },
    /** 設定を読み込みます */
    loadSettings (state) {
      const settings = JSON.parse(localStorage.getItem('settings'))
      if (settings) {
        state.settings = Object.assign(state.settings, settings)
      }
    },
    saveAuthorized(state, isAuthorized) {
      state.isAuthorized = isAuthorized;
    },
  },
  actions: {
    /** 設定を保存します */
    saveSettings ({ commit }, { settings }) {
      commit('saveSettings', { settings })
    },
    /** 設定を読み込みます */
    loadSettings ({ commit }) {
      commit('loadSettings')
    },
    saveAuthorized({ commit }, isAuthorized) {
      commit('saveAuthorized', isAuthorized);
    },
  },
  modules: {
  }
})
